h1 {
  text-align: center;
  color: #333; }

.description {
  background-color: rgba(255, 255, 255, 0.3);
  padding: 10px;
  margin-bottom: 26rem; }

.burgr-container {
  cursor: pointer;
  display: inline-block;
  background-color: #fff;
  padding: 10px;
  margin-bottom: 8px; }
  .burgr-container:nth-last-child(n+2) {
